*{
  margin: 0;
}

body{
  background-color: #f5f6fa;
}

.app{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.app__header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.app__header h1{
  color: #fc3c3c;
  font-size: 2rem;
}

.app__dropdown {
  background-color: white;
}

.app__left{
  flex: 0.9;
}

.app__right {
  display: flex;
  flex-direction: column;
}

.app__graphTitle {
  margin-top: 20px;
  margin-bottom: 20px;
}

.app__right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.app__graph {
  flex-grow: 1;
}

.app__stats{
  display: flex;
  justify-content: space-between;
}

@media (max-width: 500px) {
  .app{
    flex-direction: column;
  }
  .app__stats{
    flex-direction: column;
  }
  .app__header{
    flex-direction: column;
  }
  .app__header h1{
   margin-bottom: 25px; 
  }
  .app__right{
    margin-top: 20px;
  }
}

@media (max-width: 990px) {
  .app{
    flex-direction: column;
  }
}